<template lang="pug">
    div.login-index
        div.form-group.form-group-mobile
            input(type="number" v-model="mobile" maxlength="11" placeholder="请输入您的手机号码")
            span.btn-send-verify(@click="sendVerify") 验证
        div.form-group
            input(type="number" v-model="verify_coder" placeholder="输入您的验证码")
        div.btn-group
            mt-button(type="primary" @click="doLogin") 登录
</template>
<script>
import { Toast, Indicator } from 'mint-ui';
import queryString from 'queryString'
// import { $checkAction } from '@/assets/js/fanweUnity';
export default {
    name: 'loginIndex',
    data() {
        return {
            mobile: '',
            verify_coder: '',
            is_disabled: false,
            code_lefttime: 0,
            sendVerifyText: '验证',
            code_timeer: null
        }
    },
    methods: {
        check() {
            // if($checkAction.checkEmpty(this.mobile)) {
            //     Toast('请输入手机号码');
            //     return false;
            // }
            // if(! $checkAction.checkMobilePhone(this.mobile)) {
            //     Toast('请输入有效的手机号码');
            //     return false;
            // }
            // else if($checkAction.checkEmpty(this.verify_coder)) {
            //     Toast('请输入验证码');
            //     return false;
            // }
            // else {
            //     return true;
            // }
        },
        doLogin() {
            // if(this.check()) {
            let self = this;
            Indicator.open();
            self.axios.post(self.api.mobileLogin(), queryString.stringify({
                mobile: self.mobile,
                verify_coder: self.verify_coder
            })).then(res => {
                Indicator.close();
                if (res.data.status == 1) {
                    if (res.data.first_login) { // 用户首次登录，更新信息
                        let ToastObj = Toast(res.data.error);
                        setTimeout(function() {
                            ToastObj.close();
                            self.$router.push({
                                path: '/login/register',
                                query: {
                                    user_id: res.data.user_id
                                }
                            });
                        }, 1000);
                    }
                    else {
                        let instance = Toast(res.data.error || '操作成功');
                        let redirect = decodeURIComponent(self.$route.query.redirect || '/'); // 重定向url
                        setTimeout(() => {
                            instance.close();
                            self.$router.push({
                                path: redirect
                            });
                        }, 1000);
                    }

                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
                .catch(err => {
                    Toast(err || '网络异常，请求失败');
                });
            // }
        },
        sendVerify() {
            if (this.is_disabled) {
                return;
            }
            this.axios.post(this.api.sendMobileVerify(), queryString.stringify({
                mobile: this.mobile
            })).then(res => {
                if (res.data.status == 1) {
                    Toast('验证码已发送');
                    // 验证码倒计时
                    this.code_lefttime = 60;
                    this.leftTimeFun();
                }
                else {
                    Toast(res.data.error || '请求出错');
                }
            })
                .catch(err => {
                    Toast(err || '网络异常，请求失败');
                })
        },
        leftTimeFun() {
            // 验证码倒计时
            clearTimeout(this.code_timeer);
            this.sendVerifyText = `重新发送 ${this.code_lefttime}`;
            this.code_lefttime--;
            if (this.code_lefttime > 0) {
                this.is_disabled = true;
                this.code_timeer = setTimeout(function() { this.leftTimeFun(); }, 1000);
            }
            else {
                this.code_lefttime = 60;
                this.is_disabled = false;
                this.sendVerifyText = "发送验证码";
            }
        }
    },
    watch: {
        mobile() {
            // 手机数限制11位
            this.mobile = this.mobile.slice(0, 11);
        }
    }
}
</script>
<style lang="less" scoped>
input {
    width: 100%;
    height: 2rem;
    border: 0;
    padding: 0 .5rem;
}

.btn-group {
    padding: .5rem;
    margin-top: 2.5rem;
}

.form-group {
    margin-top: .5rem;
}

.mint-button--primary {
    background: #fee204;
    width: 100%;
    border-radius: 10rem;
}

.form-group-mobile {
    position: relative;
    .btn-send-verify {
        position: absolute;
        top: .25rem;
        right: .5rem;
        width: 5.5rem;
        height: 1.5rem;
        line-height: 1.5rem;
        display: block;
        text-align: center;
        background: #aaa;
        color: #fff;
        border-radius: 5px;
    }
}
</style>


